import React, { useState, useEffect } from "react";

import ArrowBackIcon from "@mui/icons-material/ArrowBack";

import Modal from "@material-ui/core/Modal";

function ProfilesComponent({
  buses,
  profiles,
  active,
  changeProfile,
  changeActiveProfile,
}) {
  const [open, setOpen] = useState(false);

  useEffect(() => {
    const el = document.querySelector(".profiles .items .active");
    el.scrollIntoView({ inline: "center" });
  }, []);

  const handleMaodalOpen = () => setOpen(true);
  const handleModalClose = () => setOpen(false);

  const profiles_arr = profiles.map((item) => item.name);

  return (
    <div className="profiles">
      <div className="items">
        {profiles.map((item, i) => {
          const isActive = active.name === item.name;
          return (
            <div
              key={i}
              className={isActive ? "item active" : "item"}
              onClick={() => {
                changeActiveProfile(item.name);
              }}
            >
              {item.name}
            </div>
          );
        })}
        <div className="item add" onClick={handleMaodalOpen}>
          
        </div>
      </div>
      <div className="shadow"></div>

      <Modal open={open} onClose={handleModalClose}>
        <div className="modal_profiles">
          <div className="direction">
            <div className="back" onClick={handleModalClose}>
              <ArrowBackIcon />
            </div>
            <div className="title"></div>
          </div>
          <div className="content">
            <div className="items">
              {Object.keys(buses).map((item, i) => {
                const isActive = profiles_arr.indexOf(+item) !== -1;
                return (
                  <div
                    className={`item ${isActive ? "active" : ""}`}
                    key={i}
                    onClick={() => changeProfile(+item)}
                  >
                    {item}
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
}

export default ProfilesComponent;
