import React, { useEffect, useState } from "react";
import axios from "axios";

import Skeleton from "@material-ui/lab/Skeleton";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";

import RaspAutobusComponent from "./components/RaspAutobus";

import "./styles/IndexPage.sass";

export default function IndexPage() {
  const [buses, setBuses] = useState();
  const [profiles, setProfiles] = useState();
  const [helts, setHelts] = useState();

  useEffect(() => {
    fetchData();
  }, []);

  async function fetchData() {
    let profiles = JSON.parse(localStorage.getItem("profiles"));
    let buses = JSON.parse(localStorage.getItem("buses"));
    let helts = JSON.parse(localStorage.getItem("helts"));

    const status = JSON.parse(localStorage.getItem("status"));

    if (!status) {
      localStorage.clear("profiles");
      localStorage.clear("buses");
      localStorage.clear("helts");
      localStorage.setItem("status", "true");
    }

    if (!profiles) {
      profiles = {
        buse: [{ name: 14 }, { name: 29 }, { name: 31 }],
        activeBuse: { name: 14 },
      };
      localStorage.setItem("profiles", JSON.stringify(profiles));
    }
    setProfiles(profiles);

    if (!buses) {
      buses = await axios
        .get(`${process.env.REACT_APP_URL}/buses`)
        .then((res) => {
          const buses = res.data;
          const busesObj = {};
          for (let i = 0; i < buses.length; i++) {
            busesObj[buses[i].number] = buses[i];
          }
          return busesObj;
        });
      helts = {};
      Object.keys(buses).map(
        (item) => (helts[item] = { 0: [], 1: [], dir: [] })
      );
      localStorage.setItem("helts", JSON.stringify(helts));
      localStorage.setItem("buses", JSON.stringify(buses));
    }
    setHelts(helts);
    setBuses(buses);
  }

  return (
    <div id="index-page">
      {buses ? (
        <RaspAutobusComponent
          buses={buses}
          init_profiles={profiles}
          init_helts={helts}
        />
      ) : (
        <SkeletonBuseRasp />
      )}
    </div>
  );
}

function SkeletonBuseRasp() {
  return (
    <div className="rasp-autobus">
      <div className="section-title">Bus schedule</div>

      <div className="profiles" style={{ marginBottom: "15px" }}>
        <div className="items">
          {[6, 14, 29].map((item, i) => (
            <Skeleton variant="rect" key={i} style={{ marginRight: "8px" }}>
              <div className="item">{item.name}</div>
            </Skeleton>
          ))}
        </div>
      </div>

      <div className="autobuses">
        <Skeleton variant="rect" style={{ width: "100%", margin: "0 auto" }}>
          <div className="route">: 6</div>
        </Skeleton>
        {[1, 2].map((item, i) => (
          <div className="direction" key={i}>
            <Skeleton variant="rect">
              <div className="title">
                <CompareArrowsIcon />  -   
              </div>
            </Skeleton>
            {[1].map((item, k) => (
              <div className="helt" key={k}>
                <Skeleton variant="rect">
                  <div className="title">: </div>
                </Skeleton>
                <div
                  className="times"
                  style={{ justifyContent: "space-between" }}
                >
                  {[1, 2, 3].map((item, y) => (
                    <Skeleton variant="rect" key={y}>
                      <div className="item">
                        <div className="time">15:23</div>
                        <div className="time-to"> 15 </div>
                      </div>
                    </Skeleton>
                  ))}
                </div>
                <Skeleton variant="rect">
                  <div className="full-rasp"> ...</div>
                </Skeleton>
              </div>
            ))}
          </div>
        ))}
        <Skeleton variant="rect" style={{ maxWidth: "100%" }}>
          <div className="actions">
            <div className="add-direction"> </div>
          </div>
        </Skeleton>
      </div>
    </div>
  );
}
